@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Typist .Cursor{display:inline-block}.Typist .Cursor--blinking{opacity:1;-webkit-animation:blink 1s linear infinite;animation:blink 1s linear infinite}@-webkit-keyframes blink{0%{opacity:1}50%{opacity:0}100%{opacity:1}}@keyframes blink{0%{opacity:1}50%{opacity:0}100%{opacity:1}}
* {
  scroll-behavior: smooth;
}

.App {
  text-align: left;
  font-family: 'Poppins';
  color: black;
  overflow: hidden;
  scroll-behavior: smooth;
}

.title {
  font-family: 'Poppins';
  text-align: left;
  color: black;
  margin-left: 22vw;
  margin-right: 22vw;
}

.header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(1rem + 2vmin);
}

.logo-text {
  position: fixed;
  font-size: calc(1rem + 2vmin);
}

.menu-text {
  position: fixed;
  font-size: calc(0.5rem + 2vmin);
}

.link,
.menu-link {
  text-decoration: none;
  color: black;
}

.link {
  margin-left: 3vh;
  margin-right: 3vh;
}

.link:visited {
  color: black;
}

.links-container {
  display: flex; 
  text-align: center;
  align-items: center; 
  justify-content: space-between;
  font-size: calc(1rem);

  position: fixed;
  display: block;
  top: 50%;
  left: 1.5rem;
  padding: 10px 20px;
  -webkit-transform: rotate(-90deg) translateX(-50%);
          transform: rotate(-90deg) translateX(-50%);
  -webkit-transform-origin: left top;
          transform-origin: left top;
}

.top {
  top: 0;
  margin-top: 1.5rem;
}
.right {
  right: 0;
  margin-right: 1.5rem;
  text-align: right;
}
.bottom {
  bottom: 0;
  margin-bottom: 1.5rem;
}
.left {
  left: 0;
  margin-left: 1.5rem;
  text-align: left;
}

#coding {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

#designing {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.image {
  object-fit: cover;
  width: 18vw;
  height: 18vw;
  margin: 2vmin;
  border-radius: 10px 10px;
}

.row {
  display: flex;
}

.column {
  flex: 33.33% 1;
  padding: 5px;
}

.infoText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.projectTitle {
  margin: 0 !important;
  text-align: center;
  font-weight: 800;
}

.projectDescription {
  text-align: center;
  width: 60%;
  font-size: calc(0.2rem + 1vw);
}

.designTitle {
  margin: 0;
  font-weight: 800;
}

.designSubtitle {
  margin: 0;
  font-size: calc(0.2rem + 1vw);
}

.designLink {
  text-decoration: none;
  font-weight: 800;
  color: #AC81F2;
}
